import { mapMutations } from 'vuex' 

import $ from 'jquery'
// import 'static/css/iconfont/iconfont'
import BScroll from 'better-scroll'
import api from '../service/api'

let tabs = [
  {name: ''},
  {name: ''},
  {name: ''},
  {name: ''},
  {name: ''},
  {name: ''}
]

let topics = [
  {title: '', content: '2333333'},
  {title: '', content: '2333333'},
  {title: '', content: '2333333'},
  {title: '', content: '2333333'},
  {title: '', content: '2333333'},
  {title: '', content: '2333333'},
  {title: '', content: '2333333'},
  {title: '', content: '2333333'},
  {title: '', content: '2333333'},
  {title: '', content: '2333333'},
  // {title: '', content: '2333333'},
  // {title: '', content: '2333333'},
  // {title: '', content: '2333333'},
  // {title: '', content: '2333333'},
  // {title: '', content: '2333333'},
  {title: '', content: '2333333'}
]

export default {
  data () {
    return {
      beforePullDown: true,
      bubbleY: 0,
      isPullingDown: false,
      isPullUpLoad: false,
      place: '',
      topics: [],
      tabs: [],
      topics: topics,
      // topics: [],
      bsConf: {
        probeType: 3,
        click: true,
        tap: false,
        listenScroll: true,
        pullDownRefresh: true,
        scrollbar: true,
        pullUpLoad: {
          threshold: 50
        }
      },
      scrollWidth: '',
      pullDownStyle: {
        top: '-50px'
      },
      pullDownInitTop: -50,
      activeNavIndex: 0,
      titleBarStyle: {
        width: '',
        left: '',
        right: ''
      },
      isTitleBarForward: true
    }
  },
  computed: {
    titleBarClass () {
      return {
        'backward': !this.isTitleBarForward,
        'forward': this.isTitleBarForward
      }
    }
  },
  created () {
    // this.fetchData()
    
  },
  mounted () {
    this.tabs = tabs
    this.$nextTick(() => {
      this.setScrollWidth() 
      this.scroll = new BScroll(this.$refs.titles, {
        scrollX: true,
        scrollY: false,
        tap: true
      })
      this.scroll.on('touchEnd', pos => {
        console.log(pos)
      })
      // this.scroll.on('tap', (e,a,b) => {
      //   console.log(e)
      //   debugger
      // })
      let wrapperDom = document.querySelector('.scroll-titles')
      let titlesDom = document.querySelectorAll('.title')
      wrapperDom.addEventListener('tap', e => {
        // debugger
        this.onNavTap(e)
      }, false)

    })
  },
  methods: {
    ...mapMutations([
      'RECORD_ENTRANCE_PAGE'
    ]),
    gotoTopic (e, topic) {
      this.RECORD_ENTRANCE_PAGE({
        name: 'Topic',
        id: this.$route.params.id
      })
      this.$router.push({name: 'Topic', query: {page: 1}})
      // this.$router.push({
      //   path: `/topic?page=1`
      // })
    },
    onClick (e, item, idx) {
      console.log(e)
    },
    onNavTap (e) {
      let $el = $(e.target)
      let idx = $(e.target).index()
      this.activeNavIndex = idx
      // this.isTitleBarForward = true
      // this.titleBarStyle = Object.assign({}, this.titleBarStyle, {
      //   left: `${e.target.offsetLeft}px`,
      //   right: `${e.target.offsetLeft + e.target.offsetWidth}px`
      // })
    },
    fetchData () {
      api.getTopics()
        .then(res => {
          this.topics = res.data
          debugger
        })
        .catch(err => {

        })
    },
    setScrollWidth () {
      
    },
    filter () {
      console.log('---filter')
    },
    onScroll (pos) {

    },
    onPullingDown () {
      const me = this
      this.beforePullDown = false
      this.isPullingDown = true
      // 
      setTimeout(() => {
        // if (Math.random() > 0.5) {
        if (Math.random() > -1) {
          // 
          this.topics.unshift({title: `-${Date.now()}`, content: ''})
          this.$nextTick(() => {
            this.$refs.scroll.scroll.finishPullDown()
            this.isPullingDown = false
            setTimeout(() => {
              this.pullDownStyle = `top:${this.pullDownInitTop}px`
              this.beforePullDown = true
              this.$refs.scroll.scroll.refresh()
            }, this.$refs.scroll.scroll.options.bounceTime)
          })
        } else {
          // 
          // this.$refs.scroll.forceUpdate()
        }
      }, 2000)
    },
    onPullingDownScroll (pos) {
      if (this.beforePullDown) {
        this.bubbleY = Math.max(0, pos.y + this.pullDownInitTop)
        this.pullDownStyle = Object.assign({}, this.pullDownStyle, {
          top: Math.min(pos.y + this.pullDownInitTop, 10) + 'px'
        })
      } else {
        this.bubbleY = 0
      }
    },
    onPullingUp () {
      this.isPullUpLoad = true
      setTimeout(() => {
        if (Math.random() > -1) {
          this.topics.push({title: `-${Date.now()}`, content: ''})
          this.$nextTick(() => {
            this.isPullUpLoad = false
            this.$refs.scroll.scroll.finishPullUp()
            // this.pullUpDirty = dirty
            this.$refs.scroll.scroll.refresh()
          })
        }
      }, 2000)
    }
  }
}



